'use client'
import { AgGridReact } from "ag-grid-react";
import { TournamentTableElementProps } from "./TournamentTableElement.interface";
import { ColDef } from "ag-grid-community";
import { TeamElementTable } from "./TeamElementTable.type";

export default function TournamentTableElement(props : TournamentTableElementProps) {

    const columnDefs : ColDef<TeamElementTable>[] = [
        { field: 'teamName', headerName: props.groupName ? props.groupName : "Группа" },
        { field: 'games', headerName: 'И' },        // Игры
        { field: 'wins', headerName: 'В' },          // Победы
        { field: 'draw', headerName: 'Н' },          // Ничьи
        { field: 'defeat', headerName: 'П' },        // Поражения
    {
        headerName: 'М',                       // Название столбца
        valueGetter: (params) => `${params.data?.scoredGoals}-${params.data?.missedGoals}`
    }, 
        { field: 'score', headerName: 'О' }
    ]

    return (
        <AgGridReact
            rowData={props.teams}
            columnDefs={columnDefs}
            domLayout="autoHeight"
        />
    )
}